
export default function ItemTableHeader({languages, onClick}) {
    const titles =  ["Name", ...languages.map(lang => `Name:${lang}`)];
    return (
        <tr>
        {titles.map(title => 
            (<th 
                style={{cursor: "pointer"}}
                key={title}
                onClick={() => onClick(title.toLowerCase())}
            >{title}
            </th>))
        }
        </tr>
    );
}